

.auth_form {
    font-size: 20px;
    display: grid;
    width: 300px;
    grid-template: "a"
                    "b";
}

.auth-forms {
    display: grid;
    grid-template: "a"
                    "b";
    row-gap: 10px;
}

.auth-buttons {
    display: flex;
    margin-top: 20px;
    width: 175px;
    justify-content: space-between;
}